const fs = require('fs');

let css = '';

try {
    // css = fs.readFileSync('./build/css/index.css', 'utf-8');
    css = fs.readFileSync('./src/css/main.css', 'utf-8') + fs.readFileSync('./src/css/header.css', 'utf-8')
}
catch {
    console.log('CSS not built.')
}

module.exports = () => () => {

    const $style = document.createElement('style');
    $style.textContent = css;
    return $style;

};
