require('babel-polyfill'); // adds about 200ms to tests.

const _constants = require('./constants');
const _lib = require('./lib');
const _stores = require('./stores');
const _services = require('./services');
const _components = require('./components');
const _styles = require('./styles');
const _views = require('./views');
const _context = require('./context');
const _start = require('./start');

module.exports = ({ container, fetch = window.fetch, random = Math.random }) => {

    const constants = _constants();

    const context = _context(container);
    const lib = _lib({ random });
    const stores = _stores({ lib });
    const services = _services({ lib, constants, stores });
    const components = _components({ lib });
    const styles = _styles({ lib, stores, services });
    const views = _views({ lib, constants, stores, services, components, context });
    const start = _start({ constants, stores, services, styles, views, context });

    const app = {
        lib,
        stores,
        constants,
        services,
        components,
        styles,
        views,
        start
    };

    // For demo & debug purposes only.
    // if (!window.agileAvatars) window.agileAvatars = [];
    // window.agileAvatars.push(agileAvatars);

    return app;
};
