const relatedTags = require('../../../../../data/tags.json')


module.exports = ({ stores, views }) => bookmarkId => {

    const bookmark = stores.bookmarks.getState(bookmarkId);

    const $bookmark = document.createElement('div');


    const $link = document.createElement('a');
    $link.href = bookmark.url;

    const $title = document.createElement('div');
    $title.textContent = bookmark.title;
    $title.className = 'title';
    $link.append($title);

    const $url = document.createElement('div');
    $url.textContent = bookmark.url;
    $url.className = 'url';
    $link.append($url);


    $bookmark.append($link);


    const $info = document.createElement('div');
    $info.textContent = `${bookmark.by} on ${bookmark.on}`;
    $bookmark.append($info);


    const $tags = document.createElement('div');
    $tags.className = 'tags';


    const collectTags = tags => {
        if (!tags) return tags;
        const moreTags = tags.flatMap(tag => {
            return collectTags(relatedTags[tag]);
        })

        return tags.concat(moreTags || [])
    }

    const allTags = new Set(collectTags(bookmark.tags));

    console.log(allTags)

    allTags.forEach(tag => {
        if (!tag) return;

        const $tag = document.createElement('div');
        $tag.textContent = tag;
        $tag.className = 'tag';

        if (!bookmark.tags.includes(tag)) {
            $tag.classList.add('derived');
        }

        $tags.append($tag);

    });

    $bookmark.append($tags);


    return $bookmark;

};
