module.exports = (obj) => arg => {

    const name = Object.keys(obj)[0];
    const tree = Object.values(obj)[0];

    const product = {};
    const finalArg = { ...arg, [name]: product };

    const deepMap = function (obj, initial = {}) {
        return Object.keys(obj).reduce(function (acc, k) {
            if ({}.toString.call(obj[k]) == '[object Object]') {
                acc[k] = deepMap(obj[k])
            } else {
                const val = obj[k];
                const isFunction = typeof val === 'function';
                acc[k] = isFunction ? val(finalArg) : val;
            }
            return acc
        }, initial)
    }

    return deepMap(tree, product);

};