module.exports = ({ constants, stores, services, views, styles, context }) => () => {

    /*
    Append styles (also wires events).
    */
    context.styles.append(styles.main());

    /*
    Append root view (also wires events).
    */
    context.views.append(views.app());

    /*
    Load initial state (triggers events).
    TODO: Refactor.
    */
    const initialisers = {
        bookmarks: bookmarks => bookmarks.forEach(stores.bookmarks.insert)
    };

    Object.keys(stores).forEach(key => {
        const state = constants.initialState[key];
        if (!state) return;
        initialisers[key](state);
    });

};
